<template>
  <div>
    <el-popover placement="bottom" width="450" trigger="click">
      <div class="title">
        <div class="current-city">当前城市：北京</div>
        <el-cascader size="mini" placeholder="试试搜索：指南" :options="options" filterable></el-cascader>
      </div>
      <div class="hot-city">
        <ul>
          <li v-for="city in hotCityList" :key="city">{{ city }}</li>
        </ul>
      </div>

      <div class="city-list">
        <div class="city">城市</div>
        <ul class="city-city-letter">
          <li v-for="first in cityListSortFirst" :keys="first" @click="scrollIntoView(first)">{{ first }}</li>
        </ul>

        <div class="slimScrollDiv" style="position: relative; overflow: hidden; width: auto; height: 130px;">
          <el-scrollbar style=" height: 130px;">
            <dl class="city-city">
              <template v-for="(value, key) in cityListSort">
                <dt :class="[`index${key}`]" :ref="`index-${key}`">{{ key }}</dt>
                <dd>
                  <li v-for="city in value">{{ city.name }}</li>
                </dd>
              </template>

            </dl>
            <div class="slimScrollBar"
              style="background: rgb(0, 0, 0); width: 3px; position: absolute; top: 0px; opacity: 0.4; display: none; border-radius: 7px; z-index: 99; right: 1px; height: 30px;">
            </div>
            <div class="slimScrollRail"
              style="width: 3px; height: 100%; position: absolute; top: 0px; display: none; border-radius: 7px; background: rgb(51, 51, 51); opacity: 0.2; z-index: 90; right: 1px;">
            </div>
          </el-scrollbar>
        </div>
      </div>
      <el-button slot="reference">click 激活 <i class="el-icon-caret-bottom"></i></el-button>
    </el-popover>
  </div>

</template>

<script>
const citys = require('./provinceCityDatas.json')
import pinyin from 'js-pinyin'
export default {
  name: 'city',
  data() {
    return {
      options: [],
      hotCityList: [
        "全国",
        "北京",
        "天津",
        "沈阳",
        "大连",
        "上海",
        "南京",
        "苏州",
        "杭州",
        "青岛",
        "郑州",
        "武汉",
        "长沙",
        "广州",
        "深圳",
        "重庆",
        "成都",
        "西安",
        "香港",
        "澳门",
      ],
      cityListSort: [],


      cityjson: {},
    }
  },
  computed: {
    cityListSortFirst() {
      return Object.keys(this.cityListSort);
    },
  },
  created() {
    let cityList = [];
    let FristPin = ["A", "B", "C", "D", "E", "F", "G", "H", "J", "K", "L", "M", "N", "P", "Q", "R", "S", "T", "W", "X", "Y", "Z"];
    citys.children.forEach((item) => {
      if (item.name.includes("市") || item.name.includes("台湾") || item.name.includes("行政区")) {
        cityList.push(item);
      } else {
        item.children.forEach((city) => {
          cityList.push(city);
        });
      }
    });
    cityList.forEach(item => {
      item.name = item.name.replace(/市|地区|特别行政区/g, '');
    })
    pinyin.setOptions({ checkPolyphone: false, charCase: 0 });
    //先拿到全部城市的名字
    let cityArr = [];
    for (let i = 0; i < cityList.length; i++) {
      //遍历数组,拿到城市名称
      let cityName = cityList[i].name;
      //取全部城市的首字母
      let fristName = pinyin.getCamelChars(cityName).substring(0, 1); //这里截取首字母的第一位
      //给原json添加首字母键值对
      cityList[i].first = fristName;
      //放入新数组
      cityArr.push(cityList[i]);
    }
    let cityJson = {};
    console.log(cityArr)
    //根据首字母键值对给原数据按首字母分类
    for (let i = 0; i < FristPin.length; i++) {
      //这里的FirstPin是一个写入了所有字母的数组,见data中
      cityJson[FristPin[i]] = cityArr.filter(function (value) {
        return value.first === FristPin[i];
      });
    }
    console.log(cityJson)
    this.cityListSort = cityJson;
  },
  methods: {
    scrollIntoView(e) {
      this.$nextTick(() => {
        console.log(this.$refs[`index-${e}`])
        this.$refs[`index-${e}`][0].scrollIntoView({ behavior: 'smooth' });
      })
    },
  },
}
</script>

