<template>
  <div class="container">
    <List @add-img-to-popup="addImgToPopup"></List>
    <div id="map"></div>
  </div>
</template>

<script>
import List from './list.vue'
export default {
  name: "demo",
  data() {
    return {
      marker: null,
      count: 0,
      map: null
    }
  },
  components: {
    List
  },
  created() {
    // 解决地图拖动黑线问题
    var originalInitTile = L.GridLayer.prototype._initTile
    L.GridLayer.include({
      _initTile: function (tile) {
        originalInitTile.call(this, tile);
        var tileSize = this.getTileSize();
        tile.style.width = tileSize.x + 1.5 + 'px';
        tile.style.height = tileSize.y + 1.5 + 'px';
      }
    });
  },
  mounted() {
    this.initMap();
  },
  methods: {
    addImgToPopup() {
      this.count = this.count + 1;
      let startStr = `<div id="rightClick" style="display:flex;flex-wrap: wrap; width:${this.count * 200 + 50}px;max-width:1050px">`
      let endStr = '</div>';
      let imgStr = '<div class="item" style="display:flex;margin-top:10px"><img style="width:100px" src="https://gd-hbimg.huaban.com/8c62d1d160008bcf075937a8571c8470969d62816ded1-SDPlMm_fw1200webp" /><p style="width:100px;margin-left:10px"><span>高度：1100km</span><br><span>名称：张三</span><br><span>距离：1100km</span></p></div>';
      const str = [startStr]
      for (let i = 0; i < this.count; i++) {
        str.push(imgStr)
      }
      str.push(endStr)
      console.log(str.join(''))
      this.marker.setPopupContent(str.join(''))
      document.getElementById("rightClick").addEventListener('dblclick', (e) => {
        const myElement = document.getElementById("rightClick")
        var target = e.target;
        while (target && target !== myElement) { // 遍历直到body或者找到myElement
          if (target.classList.contains('item')) {
            // 如果找到了具有类名itemaaa的元素，执行删除操作
            // target.parentNode.removeChild(target);
            this.count = this.count - 2;
            this.addImgToPopup()
            return; // 退出事件处理函数
          }
          target = target.parentElement; // 向上移动到父元素
        }

        // 如果没有找到类名为itemaaa的元素，可以在这里添加其他操作
        console.log('双击的元素中没有找到类名为item的元素。');
      })
    },
    initMap() {
      var that = this;
      //限制地图的拖动范围是正负90到正负180，这样才合理。
      var corner1 = L.latLng(-90, -180); //设置左上角经纬度
      var corner2 = L.latLng(90, 180);	//设置右下点经纬度
      var bounds = L.latLngBounds(corner1, corner2); //构建视图限制范
      this.map = L.map('map', {
        center: [19.7294, -155.0907],
        // crs: L.CRS.Baidu,
        zoom: 5,
        // 设置地图的最大边界

        // maxBounds: bounds,
        // 防止地图缩小到显示边界外的范围
        // maxBoundsViscosity: 1.0
      })

      let mapUrl = {
        'google': 'https://{s}.google.com/vt/lyrs=s&x={x}&y={y}&z={z}',
        'gaode': 'https://webrd04.is.autonavi.com/appmaptile?lang=zh_cn&size=1&scale=1&style=7&x={x}&y={y}&z={z}',
        'gaode1': 'https://webst01.is.autonavi.com/appmaptile?style=6&x={x}&y={y}&z={z}',
        'gaode2': "https://webst01.is.autonavi.com/appmaptile?style=8&x={x}&y={y}&z={z}",
        // 'google1':'https://mt1.google.com/vt/lyrs=s&x={x}&y={y}&z={z}',
        // 'google2':'https://mt1.google.com/vt/lyrs=m&x={x}&y={y}&z={z}',
        'google1': 'https://mt3.google.com/maps/vt?lyrs=s%40781&hl=zh-CN&gl=CN&x={x}&y={y}&z={z}',
        'google2': 'https://mt3.google.com/maps/vt?lyrs=h%40781&hl=zh-CN&gl=CN&x={x}&y={y}&z={z}',
        'baidu': 'http://maponline1.bdimg.com/tile/?qt=vtile&x={x}5&y={y}&z={z}'
      }

      // let mapUrl = 'https://{s}.google.com/vt/lyrs=s&x={x}&y={y}&z={z}'
      var noLabelsLayer = L.tileLayer(mapUrl.gaode, {
        maxZoom: 19,
        minZoom: 3,
        // tms: true,
        subdomains: ['mt0', 'mt1', 'mt2', 'mt3']
      })


      // // 添加有路网标记的地图图层
      // var labelsLayer = L.tileLayer(mapUrl.gaode2, {
      //   maxZoom: 19,
      //   minZoom: 3,
      // })


      // 创建一个叠加图层对象
      var overlayLayers = {
        "底图": noLabelsLayer,
        // "线路图": labelsLayer
      };

      // 默认添加两个图层到地图
      noLabelsLayer.addTo(this.map);
      // labelsLayer.addTo(this.map);

      // 创建图层控制控件并添加到地图
      L.control.layers(null, overlayLayers, { collapsed: false }).addTo(this.map);

      let marker = [19.7294, -155.0907]; //, [39.952114, 116.401682], [37.981008, 114.732127]];

      this.marker = L.marker(marker).addTo(this.map).bindPopup('Marker', {
        maxWidth: 1050,
        autoClose: false
      }).openPopup()
      // 定义矩形的地理边界纬度、经度
      var bounds = [[[41.016203, 113.094768], [39.136648, 117.822873]], [[33.096727, 97.181106], [28.784995, 112.634835]], [[63.6908, -145.0157], [64.0260, -146.0157]]];
      bounds.forEach((i, k) => {
        L.rectangle(i, { color: "red", weight: 2, fillOpacity: 0 }).addTo(this.map).bindPopup('矩形信息' + k);
      })


      var listDom = document.getElementById('list');

      listDom.addEventListener('dragstart', function (e) {
        console.log(e.target);
        e.dataTransfer.setData('text', e.target.id); // 存储元素的id
      });

      listDom.addEventListener('dragend', function (e) {
        const shape = e.target.getAttribute('data-shape');
        var latlng = that.map.mouseEventToLatLng(e);

        if (shape == 'circle') {
          // 信号范围半径（以米为单位，这里示例为10km）
          var radiusInMeters = 10000;
          // 创建圆并显示半径
          that.createCircle(latlng, radiusInMeters)
        } else {
          // 创建多边形
          that.generateIrregularPolygon(latlng, that.map);
        }

      });

    },
    createCircle(latlng, radiusInMeters) {
      var circle = L.circle(latlng, {
        color: 'red',
        fillColor: '#f03',
        fillOpacity: 0.5,
        radius: radiusInMeters
      }).addTo(this.map);

      // 获取圆心和半径
      var center = circle.getLatLng();
      var radius = circle.getRadius();

      // 计算圆的边界点
      var edgePoint = L.latLng(center.lat + (radius / 111320), center.lng);

      // 创建虚线的半径
      var dashedLine = L.polyline([center, edgePoint], {
        color: 'blue',
        dashArray: '5, 10'
      }).addTo(this.map);

      // 在虚线旁边添加竖直排列的提示
      var midPoint = L.latLng((center.lat + edgePoint.lat) / 2, (center.lng + edgePoint.lng) / 2);
      var tooltip = L.tooltip({
        permanent: true,
        direction: 'center',
        className: 'radius-tooltip'
      })
        .setContent("半径: " + radius + "m")
        .setLatLng(midPoint)
        .addTo(this.map);
    },
    generateIrregularPolygon(center, map) {
      var sides = Math.floor(Math.random() * 5) + 6;
      var points = [center];

      for (var i = 1; i <= sides; i++) {
        var edgeLength = Math.floor(Math.random() * 40) + 10;
        var angle = 2 * Math.PI * i / sides;

        var offsetLat = edgeLength * Math.sin(angle) / 111320;
        var offsetLng = edgeLength * Math.cos(angle) / (111320 * Math.cos(center.lat * Math.PI / 180));

        var newLat = center.lat + offsetLat;
        var newLng = center.lng + offsetLng;

        points.push(L.latLng(newLat, newLng));
      }

      points.push(points[0].clone());

      var irregularPolygon = L.polygon(points, {
        color: 'red',
        fillColor: '#f0f',
        fillOpacity: 0.5
      }).addTo(map);


      // 计算多边形的中心点
      function getPolygonCenter(irregularPolygon) {
        var latlngs = irregularPolygon.getLatLngs()[0];
        var latSum = 0, lngSum = 0;
        latlngs.forEach(function (latlng) {
          latSum += latlng.lat;
          lngSum += latlng.lng;
        });
        return [latSum / latlngs.length, lngSum / latlngs.length];
      }

      // 添加中心点的 marker
      var center = getPolygonCenter(irregularPolygon);
      var centerMarker = L.marker(center).addTo(map);

      var isDragging = false;
      var initialLatLngs = [];
      var startPoint = null;

      // 监听鼠标按下事件
      irregularPolygon.on('mousedown', function (e) {
        isDragging = true;
        startPoint = e.latlng;
        initialLatLngs = irregularPolygon.getLatLngs()[0].map(function (latlng) {
          return [latlng.lat, latlng.lng];
        });
        map.dragging.disable();
        map.on('mousemove', onMouseMove);
        map.on('mouseup', onMouseUp);
      });

      function onMouseMove(e) {
        if (!isDragging) return;

        var offsetLat = e.latlng.lat - startPoint.lat;
        var offsetLng = e.latlng.lng - startPoint.lng;

        var newLatLngs = initialLatLngs.map(function (latlng) {
          return [latlng[0] + offsetLat, latlng[1] + offsetLng];
        });

        irregularPolygon.setLatLngs([newLatLngs]);

        // 更新中心点 marker 的位置
        var newCenter = getPolygonCenter(irregularPolygon);
        centerMarker.setLatLng(newCenter);
      }

      function onMouseUp() {
        isDragging = false;
        map.dragging.enable();
        map.off('mousemove', onMouseMove);
        map.off('mouseup', onMouseUp);
      }

      // 防止多边形拖动时底图跟随拖动
      map.on('mouseup', function () {
        if (isDragging) {
          isDragging = false;
          map.dragging.enable();
          map.off('mousemove', onMouseMove);
          map.off('mouseup', onMouseUp);
        }
      });

      // 定位至多边形
      map.fitBounds(irregularPolygon.getBounds(), {
        padding: [50, 50], // 增加一些边界;
        maxZoom: 15
      });

    }




  },
}

</script>
<style scoped>
.container {
  width: 100%;
  height: 100%;
  display: flex;
}

#map {
  width: 100%;
  height: 100%;
}
</style>