<template>
    <div class="container">
        <div id='map'></div>
        <div id="categories">
            <div v-for="(item, category) in data" :key="category">
                <input type="checkbox" :id="category" :name="category" v-model="selectedCategories" :value="category" />
                <label :for="category">
                    {{ category }} ({{ item.length }})
                </label>
            </div>
        </div>
    </div>
</template>

<script>
import data from './json.json'
import { GPS } from './LngLatTranlate';
export default {
    components: {},
    data() {
        return {
            map: null,
            data: data,
            selectedCategories: [],
            markers: []
        }
    },
    // 监听属性 类似于data概念
    computed: {},
    // 监控data中的数据变化
    watch: {
        selectedCategories: {
            handler() {
                this.addMarkersToMap(); // 当选择的类别变化时，更新地图上的标记
            },
            deep: true,
        },
    },
    created() {
    },
    mounted() {
        this.initMap();
    },
    // 方法集合
    methods: {
        initMap() {

            //投影参数
            var crs = new L.Proj.CRS('EPSG:900913',
                '+proj=merc +a=6378206 +b=6356584.314245179 +lat_ts=0.0 +lon_0=0.0 +x_0=0 +y_0=0 +k=1.0 +units=m +nadgrids=@null +wktext  +no_defs',
                {
                    resolutions: [
                        262144,
                        131072,
                        65536,
                        32768,
                        16384,
                        8192,
                        4096,
                        2048,
                        1024,
                        512,
                        256,
                        128,
                        64,
                        32,
                        16,
                        8,
                        4,
                        2,
                        1,
                        0.5
                    ],
                    origin: [0, 0],
                    //bounds:L.bounds([20037508.342789244, 0], [0, 20037508.342789244])
                });

            this.map = L.map('map', {
                center: [19.645656, -155.51124154],
                crs,
                zoom: 11,
            })
            L.tileLayer('http://maponline0.bdimg.com/tile/?qt=vtile&x={x}&y={y}&z={z}&styles=pl&scaler=1&udt=20241010&from=jsapi3_0', {
                maxZoom: 19,
                tms: true,
                attribution: '&copy; <a href="https://www.openstreetmap.org/copyright">OpenStreetMap</a> contributors'
            }).addTo(this.map);

            // 添加标记
            // L.marker([19.7294, -155.0907]).addTo(this.map)
            // L.marker([19.444444,-155.275000]).addTo(this.map)
            //     .bindPopup('Marker', {
            //         maxWidth: 1050,
            //         autoClose: false
            //     }).openPopup()

            // 获取categories容器
            // const container = document.getElementById('categories');

            // // 遍历数据中的每个类别
            // Object.keys(data).forEach((category) => {
            //     // 创建复选框
            //     const checkbox = document.createElement('input');
            //     checkbox.type = 'checkbox';
            //     checkbox.id = category;
            //     checkbox.name = category;

            //     // 创建标签
            //     const label = document.createElement('label');
            //     label.htmlFor = category;
            //     label.textContent = `${category} (${data[category].length})`;

            //     // 将复选框和标签添加到容器中
            //     container.appendChild(checkbox);
            //     container.appendChild(label);
            //     container.appendChild(document.createElement('br')); // 添加换行
            // });

            this.map.on("click", function (e) {
                console.log(e.latlng);
            })
        },
        addMarkersToMap() {
            // 清除旧的标记
            this.markers.forEach(marker => marker.remove());
            this.markers = [];

            // 根据 selectedCategories 添加新的标记
            this.selectedCategories.forEach(category => {
                this.data[category].forEach(item => {
                    // WGS-84 to GCJ-02 to BD-09
                    const GCJ02 = GPS.gcj_encrypt(item.lat,item.lng);
                    const bd = GPS.bd_encrypt(GCJ02.lat,GCJ02.lon);
                    const marker = L.marker([bd.lat, bd.lon])


                    // WGS-84
                    // const marker = L.marker([item.lat, item.lng])
                        .bindPopup(`${item.name}`)
                        .addTo(this.map);
                    this.markers.push(marker);
                });
            });
            
            // eslint-disable-next-line new-cap
            const group = new L.featureGroup(this.markers);
            this.map.fitBounds(group.getBounds(),{maxZoom:12});
        },
    },
}
</script>
<style lang='scss' scoped>
.container {
    position: relative;

    #map {
        width: 100vw;
        height: 100vh;
    }

    #categories {
        position: absolute;
        bottom: 200px;
        left: 0;
        z-index: 999;
    }
}
</style>
